
.all-activities {
  display: block;
}
.activities {
  list-style-type: none;
  padding: 0;
  width: 100%;
}
.activities .activity {
  position: relative;
  background-color: #fff;
  border: 1px solid #ccc;
  margin: 10px 0;
  text-align: left;
  font-size: 1.4rem;
}
.activities .activity .datetime {
    display: block;
    padding: 5px;
    font-size: 1.2rem;
}
.activities .activity:hover {
    background-color: #f4f4f4;
}
.activities .activity a {
    padding: 20px 10px;
    color: #000;
    display: block;
    text-decoration: none;
    z-index: 100;
}
.activities .activity .title {
    margin-top: 10px;
    padding: 10px;
}
.activities .activity .fa-icon {
    position: absolute;
    vertical-align: middle;
    color: rgba(100, 100, 100, 0.3);
    right: 20px;
    bottom: 0;
}
@media (max-width: 780px) {
.activities .activity {
    font-size: 2.8rem;
}
.activities .activity .datetime {
      padding: 15px;
      font-size: 2rem;
}
}



