
.g-wrapper {
  width: 100%;
}
.g-inner {
  margin: auto;
  width: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.content {
  width: 100%;
}
.header {
  margin: auto;
  padding: 3rem 0;
  background-image: url("./assets/macbook.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.header .header-text {
    display: inline-block;
    font-size: 6vw;
    padding: 3vw 3vh;
    border-radius: 3rem;
    background-color: rgba(255, 255, 255, 0.8);
}
.main-content {
  margin: 0 auto;
  padding: 30px 0;
  width: 100%;
  background-color: #f9f9f9;
}
.main-content:nth-child(odd) {
    background-color: #fff;
}
.main-content-title {
  display: block;
  background-color: #fff;
}
.sub-header-text {
  display: inline-block;
  padding: 0 30px 10px;
  border-bottom: 1px solid;
  font-size: 2.4rem;
}
.activity-wrapper {
  display: block;
  max-width: 780px;
  margin: 0 auto;
}
.footer {
  padding: 10px 0 10px;
  background-color: #000;
  color: #fff;
}



